
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2vh 12vw;
    position: fixed;
    width: 100%;
    font-size: 1.1vw;
    background-color: transparent;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    transition: background-color 0.3s ease;
  }
  .navbar.scrolled {
    background-color: var(--cor-principal);
  }
  .logo_navbar {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 1.1vw;
    font-weight: 700;
    color: var(--cor-branco);
    text-decoration: none;
  }
  
  .list_navbar {
    display: flex;
    list-style: none;
  }
  
  .item_navbar {
    margin: 0 10px;
  }
  
  .item_navbar a {
    text-decoration: none;
    color: var(--cor-branco);
    border-bottom: 2px solid transparent;
    transition: border-bottom-color 0.3s ease;
  }
  
  .item_navbar a:hover {
    border-bottom-color: var(--cor-branco);
  }
  
  .link_navbar {
    text-decoration: none;
    color: var(--cor-branco);

  }
  .conteudo_navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 4vw;
  }
  
  .button_navbar {
    padding: 1.5vh 2vw;
    font-size: 20px;
    font-weight: 600;
    background-color: transparent;
    color: var(--cor-branco);
    border: 2px solid var(--cor-branco);
    border-radius: 5px;
    text-decoration: none;
    text-transform: uppercase;
    transition: 0.3s;
  }

  .button_navbar:hover {
    background-color: var(--cor-branco);
    color: var(--cor-principal);
  }
  .bar_menu{
    display: none;
  }

  @media (max-width: 1360px){
    .navbar {
      padding: 2vh 8vw;
      font-size: 1.4vw;
    }
    .button_navbar {
      padding: 1vh 2vw;
      font-size: 18px;
    }
}

.bars-menu{
  display: none;
}

@media (max-width: 768px){
    .navbar {
      justify-content: space-between;
    }
    .list_navbar {
      display: none;
    }
    .button_navbar {
      display: none;
    }
    .logo_navbar {
      font-size: 4vw;
    }
    .logo_navbar img {
      width: 40px;
      object-fit: cover;
    }
    .bar_menu{
      display: block;
      color: var(--cor-branco);
      background: transparent;
      border: 0;
    }
    .bars-menu{
      display: block;
    }
    .bar_menu:hover{
      color: var(--cor-secundaria);
    }
    .conteudo_navbar_mobile{
      background-color: var(--cor-principal);
      position: absolute;
      padding: 15px 40px;
      left: 0;
      width: 100vw;
    }
    .list_navbar_mobile{
      display: flex;
      justify-content: space-between;
      gap: 20px;
    }
    .item_navbar_mobile{
      font-size: 2.5vw;
      text-decoration: none;
      list-style: none;
    }
    .item_navbar_mobile a{
      text-decoration: none;
      color: var(--cor-branco);
      border-bottom: 2px solid transparent;
      transition: border-bottom-color 0.3s ease;
    }
    .item_navbar_mobile a:hover{
      border-bottom-color: var(--cor-branco);
    }
}